<template>
  <div class="price-list">
    <!-- <div class="ui container"> -->
      <div>
        <div class="ui input">
          <input type="text" placeholder="商品名稱" v-model="newItem" v-on:keyup.enter="addTodo" size=10>
        </div>
        <div class="ui input">
          <input type="text" placeholder="輸入數量" v-model="newItemCount" v-on:keyup.enter="addTodo" size=4>
        </div>
        <div class="ui input">
          <input type="text" placeholder="輸入金額" v-model="newItemPrice" v-on:keyup.enter="addTodo" size=4>
        </div>
      </div>
      <div class="ui compact segments">
        <div class="ui segment" v-for="item in items">
          <div class="ui disabled input">
            <input type="text" v-model="item.name" size=8>
          </div>
          <div class="ui input">
            <input type="text" v-model="item.count" size=5>
          </div>
          <div class="ui input">
            <input type="text" v-model="item.price" size=5>
          </div>
          <div class="ui right floated icon header"><i class="ui remove icon" v-on:click="removeTodo($index)"></i></div>
        </div>
      </div>
      <div>
        可用金額
        <div class="ui input">
          <input type="text" v-model="totalPrice" v-on:keyup.enter="addTodo" size=4>
        </div>
        <div class="ui button">產生組合</div>
      </div>
    <!-- </div> -->
  </div>
</template>

<script>
export default {
  name: 'price-list',
  data () {
    return {
      newItem: '',
      newItemCount: 0,
      newItemPrice: 0,
      totalPrice: 0,
      items: [
        {name: '乳油木香皂', count: 4, price: 300},
        {name: '玫瑰精油', count: 10, price: 1400}
      ]
    }
  },
  methods: {
    addTodo: function () {
      var text = this.newItem.trim()
      var count = this.newItemCount || 0
      var price = this.newItemPrice || 0
      if (text) {
        this.items.push({name: text, count: count, price: price})
        this.newItem = ''
        this.newItemCount = 0
        this.newItemPrice = 0
      }
    },
    removeTodo: function (index) {
      console.log(index)
      this.items.splice(index, 1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>

.price-list
  padding-top 2em
  font-weight bold
  // font-size 1em
  // input
    // background-color #A5FFD6
  .ui.segment
    background #FFA69E
  .ui.button
    background-color #FF686B
    color #FFFFFF
  .ui.right.floated.icon.header
    font-size .8em
    margin-bottom 0
    margin-left 1em
    cursor pointer



</style>



